            if (item.airConditioner === conditioner && item.temperature_value === +temperature && item.drivingProfile_city === +city && item.drivingProfile_countryRoad === +countryroad && item.drivingProfile_highway === +highway){
                $("#km").animateNumbers(item.range);
            }
